import React from "react";
import { NavBar } from "../../components";
import { Redirect } from "react-router-dom";
import style from "./home.module.css";

function Home({ children, auth }) {
  return auth ? (
    <div className={style.HomeWrapper}>
      <NavBar /> {children}{" "}
    </div>
  ) : (
    <Redirect to="/login" />
  );
}

export default Home;
